<template>
<div class="">
  <ul class="main-channel-list">
    <li class="clearfix" v-for="(mItem,mIndex) in channelMData" :key="mIndex"
      v-if="Array.isArray(lastSubData[mItem.channelMName])&&lastSubData[mItem.channelMName].length!=0">
      <div class="fl channel-img">
          <img :src="require('../../assets/images/Group'+mItem.channelMOrder+'.png')" width="121" height="161">
      </div>
      <div class="fl channel-right">
          <div class="clearfix">
            <span class="title fl">{{mItem.channelMName}}</span>
            <em class="fr more" @click="channelMore(mItem)">MORE</em>
          </div>
          <ul class="sub-channel-list">
              <li class="sub-channel-list-single" v-for="(item,index) in lastSubData[mItem.channelMName]" :key="index" >
                  <div class="clearfix">
                      <h3 class="fl sub-channel-title" @click="seeDetails(item)">{{item.title}}</h3>
                      <span class="date fr">{{item.release_date | ddMMMyyyy}}</span>
                  </div>
                  <div class="infor-brief">{{item.tex}}</div>
              </li>
          </ul>
      </div>
    </li>
  </ul>
  <sub-channel-news-details :visible.sync='detailsVisible' :detailsData = "detailsData"></sub-channel-news-details>
</div>
</template>
<script>
  import SubChannelNewsDetails from '@/modules/SubChannelNewsDetails.vue'
  import {channelSubList,listDatasd,listDataParticulars} from '@/api/info'
    export default {
        props:['channelMRes'],
        name: "home",
        data() {
          return {
              store : this.$store,
              detailsVisible:false,
              detailsData:{},
              homeSubChannelInfo:{},
              channelMData:[
                {channelMName:''}
              ],
              dealSubData : {
                "PRICES":[],
                "NEWS":[],
                "DATA":[],
                "ANALYSIS":[],
                "REPORTS":[],
                "ABOUT CRUDEOIL":[]
              },
            channelSubData:[],
            lastSubData:{
              "PRICES":[],
              "NEWS":[],
              "DATA":[],
              "ANALYSIS":[],
              "REPORTS":[],
              "ABOUT CRUDEOIL":[]
            },     //处理之后的最终data格式
          }
        },

        components:{
          SubChannelNewsDetails
        },
        watch:{
          channelMRes(val){
            this.channelMData = val
          }
        },
        mounted(){

          this.channelMData = this.channelMRes
          this.initSubData()
        },
        methods:{

            channelMore(item){
                let path = item.channelMCode.split(' ').join('_').toLowerCase();
                this.$router.push(path)
            },
            initSubData(){
              let sendData = {
                  channelMDesc:'CPY'
              }
              channelSubList(sendData).then((res)=>{
                  if(res.status == 0){
                      console.log(res);
                    this.channelSubData = res.datas
                    this.dealSubDataFun()
                    this.getSubChannelFirstOneData()

                  }
              })
            },
          dealSubDataFun(){
                this.channelSubData.forEach((field,index)=>{
                    if(field.channelMId == '1006'){
                      if(this.dealSubData["PRICES"].length==2){
                        return;
                      }
                      this.dealSubData["PRICES"].push(field)
                    }
                    if(field.channelMId == '1007'){
                      if(this.dealSubData["NEWS"].length==2){
                        return;
                      }
                      this.dealSubData["NEWS"].push(field)
                    }
                    if(field.channelMId == '1009'){
                      if(this.dealSubData["DATA"].length==2){
                        return;
                      }
                      this.dealSubData["DATA"].push(field)
                    }
                    if(field.channelMId == '1010'){
                      if(this.dealSubData["ANALYSIS"].length==2){
                        return;
                      }
                      this.dealSubData["ANALYSIS"].push(field)
                    }
                    if(field.channelMId == '1012'){
                      this.dealSubData["REPORTS"].push(field)
                    }
                    if(field.channelMId == '1013'){
                      if(this.dealSubData["ABOUT CRUDEOIL"].length==2){
                        return;
                      }
                      this.dealSubData["ABOUT CRUDEOIL"].push(field)
                    }
                })
                // console.log(this.dealSubData)
            },
            initSubListDataTest(MName){
              let channelMDesc = 'CPY'
              let timer = setInterval(()=>{
                if(Array.isArray(this.channelMData)&&this.channelMData.length>0){
                  clearInterval(timer)
                  let _self = this
                  let channelCode = this.dealSubData[MName][0].channelCode
                  this.$set(this.lastSubData,MName,[])
                  listDatasd({channelMDesc,channelCode}).then((res)=>{

                      if(Array.isArray(res.datas.infoList)&&res.datas.infoList.length>0){
                          this.lastSubData[MName].push(res.datas.infoList[0])
                      }
                  }).then((res)=>{
                    if(this.dealSubData[MName].length<2){
                      return;
                    }
                    channelCode = this.dealSubData[MName][1].channelCode
                    listDatasd({channelMDesc,channelCode}).then((res)=>{
                      if(Array.isArray(res.datas.infoList)&&res.datas.infoList.length>0){
                        this.lastSubData[MName].push(res.datas.infoList[0])
                      }
                    })
                  })
                }
              })
            },
            getSubChannelFirstOneData(){
              let timer = setInterval(()=>{
                  if(Array.isArray(this.channelMData)&&this.channelMData.length>0){
                      clearInterval(timer)
                    this.channelMData.forEach((item)=>{
                      // console.log(item.channelMName.split(' ').join(''))
                      this.initSubListDataTest(item.channelMCode)
                    })
                  }
              },0)



            },
            seeDetails(item){
              listDataParticulars({infoUuid:item.uuid}).then((res)=>{
                  if(res.status == 0){
                    this.detailsVisible = true
                    this.detailsData = res.datas
                  }
              })
            }
        }
    }
</script>

<style scoped>
  .main-channel-list{
    width: 840px;
  }
  .main-channel-list .channel-right{
    width: 691px;
  }
  .main-channel-list>li{
    padding-bottom: 30px;
    margin-top: 30px;
    border-bottom: 1px solid #ECECEC;
  }
  .title{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #F36523;
    margin-bottom: 20px;
  }
  .more{
    font-family: ArialMT;
    font-size: 13px;
    color: #333333;
    cursor: pointer;
  }
  .channel-img{
    margin-right: 20px;
  }
  .date{
    font-family: ArialMT;
    font-size: 14px;
    color: #999999;
  }
  .sub-channel-title{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #333333;
    max-width: 600px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .sub-channel-title:hover{
    cursor: pointer;
    text-decoration: underline;
  }
  .infor-brief{
    font-family: ArialMT;
    font-size: 16px;
    color: #999999;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 10px;
  }
  .sub-channel-list .sub-channel-list-single{
    margin-top: 20px;
  }
  .sub-channel-list .sub-channel-list-single:first-child{
    margin-top: 0;
  }
</style>



